import * as componentI18n from "dojo/i18n!./nls/resources";

import Badge from "./Badge";
import { H, SFCProps } from "../Component";

export interface MarketplaceProps extends SFCProps {
    /**
     * The size of the badge: "regular" includes the badge name, "small" only includes the icon.
     * @type {string}
     */
    size?: "regular" | "small";
    /**
     * Controls whether or not to show a calcite style tooltip for the badge.
     * Default: false
     * @type {boolean}
     */
    tooltip?: boolean;
    /**
     * The direction to show the calcite tooltip.
     * Default: "bottom"
     * @type {string}
     */
    tooltipDirection?: "left" | "top" | "right" | "bottom";
}

/**
 * A content status badge for marketplace content.
 */
export default ({ size, tooltip, tooltipDirection }: MarketplaceProps, tsx: H) => (
    <Badge
        backgroundColor="#DDEEDB"
        textColor="#2B622B"
        key="marketplace-badge"
        size={size}
        text={componentI18n.marketplace}
        title={componentI18n.tooltips.marketplace}
        tooltip={tooltip ? componentI18n.tooltips.marketplace : undefined}
        tooltipDirection={tooltipDirection}
    >
        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16">
            <path fill="#196FA6" d="M15.939 13L12.832 3h-3.65L8.033 6.68 6.811 3H3.16L.054 13H4.13l.96-3.21L6.192 13h3.594l1.104-3.17.993 3.17h4.056zM3.386 12H1.41l2.486-8h1.881l-2.391 8zm2.196-3.856l.893-2.983 1.048 3.156-.895 2.871-1.046-3.044zM9.075 12H7.422l2.496-8h1.943l-2.786 8zm3.409-6.749L14.583 12h-1.966l-1.176-3.755 1.043-2.994z"/>
        </svg>
    </Badge>
);
